html {
  box-sizing: border-box;
}
*, *::before, *::after {
  box-sizing: inherit;
}


.vertical-center {
    display: flex;
    align-items: center;
    justify-content: center;
}
.text-center {
    text-align: center;
}

.fixed {
  position:fixed;
  top:0;
  z-index:99999;
}


p {
	&.error {
		color: $color-error;
	}

}


